<template>
  <div class="home">
    <HomeHeader :city="city"/>
    <HomeSwiper :list="swiperList"/>
    <Icons/>
    <PlayList/>
    <Recommand/>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeHeader from '@/components/HomeHeader.vue'
import HomeSwiper from '@/components/Swiper.vue'
import Icons from '@/components/Icons.vue'
import Recommand from '@/components/Recommand.vue'
import PlayList from '@/components/PlayList'
import axios from 'axios'

export default {
  name: 'home',
  components: {
    HomeHeader,
    HomeSwiper,
    Icons,
    Recommand,
    PlayList
  },
  data() {
    return {
      city:"",
      swiperList: []
    }
  },
  methods: {
    getHomeInfo() {
      console.log("axios.get(/api/index.json).then(this.getHomeInfoSuccess)")
      axios.get("/api/index.json").then(this.getHomeInfoSuccess)
    },
    getHomeInfoSuccess(res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.city = data.city
        this.swiperList = data.swiperList
      }
      console.log(res)
    }
  },
  mounted() {
    this.getHomeInfo()
  }
}
</script>
